.App {
  margin: 0 auto;
  text-align: center;
  width: 50%;
}

.App__Input {
  border: 1px solid #d87093;
  border-bottom: 5px solid #d87093;
  border-radius: 5px;
  margin: 5px;
  padding: 10px;
}

.App__Button {
  background-color: #d87093;
  border: 4px solid #d87093;
  border-radius: 5px;
  color: #fff;
  padding: 10px;
}

.App__Links {
  font-size: 13px;
  margin-top: 50px;
}

.App__Links a {
  color: #d87093;
}

.App__Input:focus {
  box-shadow: 0 20px 20px rgba(0, 0, 0, 0.08);
}

*:focus {
  outline: none;
}

body {
  background-color: #fafafa;
  display: flex;
  align-items: center;
  justify-content: center;
  min-height: 95vh;
  margin: 0;
  padding: 0;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
}

